<template>
  <div class="issue issue-cont">
    <el-form ref="form" label-width="80px">
      <el-form-item label="活动名称">
        <el-input></el-input>
        <el-button type="primary" v-clipboard="copyData">复制链接</el-button>
      </el-form-item>
    </el-form>
    <!-- 穿梭框 -->
    <div class="el-transfer-box" style="height:80%;">
      <p style="text-align: left; margin: 0 0 20px;font-size:16px;color:rgba(64,158,255,1);">设置投放对象</p>
      <div style="text-align: center;width:70%;margin:auto;">
        <el-transfer
          style="text-align: left; display: inline-block"
          v-model="value3"
          filterable
          :left-default-checked="[2, 3]"
          :right-default-checked="[1]"
          :render-content="renderFunc"
          :titles="['所有成员', '已选成员']"
          :button-texts="['到左边', '到右边']"
          :format="{
                    noChecked: '${total}',
                    hasChecked: '${checked}/${total}'
                    }"
          @change="handleChange"
          :data="data"
        ></el-transfer>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "issue",
  data() {
    const generateData = _ => {
      const data = [];
      for (let i = 1; i <= 15; i++) {
        data.push({
          key: i,
          label: `备选项 ${i}`
        });
      }
      return data;
    };
    return {
      data: generateData(),
      value3: [1],
      value4: [1],
      renderFunc(h, option) {
        return (
          <span>
            {option.key} - {option.label}
          </span>
        );
      },
      copyData: "这是链接"
    };
  },

  methods: {
    handleChange(value, direction, movedKeys) {
      // 选中数据
      console.log(value, direction, movedKeys);
    }
  }
};
</script>
<style scoped>
@import "./issue.scss";
@import "./index.scss";
</style>
<style>
.issue .el-form-item__content {
  width: 100%;
  display: inline-flex;
  flex-wrap: nowrap;
  margin-left: 0 !important;
  justify-content: left;
}

.issue .el-form-item__content .el-input {
  width: 70%;
  margin-right: 20px;
}
.issue .el-form-item__content .el-input__inner {
  width: 100%;
}
.issue .el-form-item__label {
  display: block;
  float: none;
  width: auto !important;
  text-align: left;
  font-size: 20px;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
  line-height: 20px;
  margin-bottom: 20px;
}
.issue .transfer-footer {
  margin-left: 20px;
  padding: 6px 5px;
}
.issue .el-checkbox + .el-checkbox {
  margin-left: 0;
}
.issue .el-transfer {
  width: 100%;
}
.issue .el-transfer .el-transfer-panel {
  width: 40%;
  height: 500px;
}
.issue .el-transfer .el-transfer__buttons {
  width: 20%;
  text-align: center;
}
.issue .el-transfer .el-transfer__buttons .el-transfer__button {
  width: 70%;
  padding-left: 0;
  padding-right: 0;
  margin-left: 0;
  margin-right: 0;
}
.issue .el-transfer-panel__filter {
  width: 100%;
  margin: 0;
  padding: 10px;
}
.issue .el-transfer-panel__item.el-checkbox {
  display: block;
}
.el-transfer-panel__body {
  height: auto !important;
}
.el-transfer-panel__list.is-filterable {
  height: 400px !important;
}
</style>





